import axios, { AxiosError, AxiosResponse } from "axios";
import { toast } from "react-toastify";
import { history } from "..";
import { PaginationResponse } from "../app/models";
import { store } from "../app/store/configure-store";
import * as ROUTES from "../routes/constants";

const sleep = () => new Promise((resolve) => setTimeout(resolve, 500));

axios.defaults.baseURL = process.env.REACT_APP_API_URL;
axios.defaults.withCredentials = true;

const responseBody = (response: AxiosResponse) => response.data;

axios.interceptors.request.use((config) => {
    const token = store.getState().account.user?.token;

    config.headers = config.headers ?? {};

    if (token) {
        config.headers.Authorization = `Bearer ${token}`;
    }

    return config;
});

axios.interceptors.response.use(
    async (response) => {
        if (process.env.NODE_ENV === "development") await sleep();

        const pagination = response.headers["pagination"];

        if (pagination) {
            response.data = new PaginationResponse(
                response.data,
                JSON.parse(pagination),
            );

            return response;
        }

        return response;
    },
    (error: AxiosError) => {
        const { data, status } = error.response as any;

        switch (status) {
            case 400:
                if (data.errors) {
                    const modelStateErrors: string[] = [];

                    for (const key in data.errors) {
                        if (data.errors[key]) {
                            modelStateErrors.push(data.errors[key]);
                        }
                    }
                    throw modelStateErrors.flat();
                }

                toast.error(data.title);

                break;

            case 401:
                toast.error(data.title);
                break;

            case 500:
                history.push({
                    pathname: ROUTES.SERVER_ERROR,
                    state: { error: data },
                });
                break;

            default:
                break;
        }

        return Promise.reject(error.response);
    },
);

const requests = {
    get: (url: string, params?: URLSearchParams) =>
        axios.get(url, { params }).then(responseBody),
    post: (url: string, body: {}) => axios.post(url, body).then(responseBody),
    put: (url: string, body: {}) => axios.put(url, body).then(responseBody),
    delete: (url: string) => axios.delete(url).then(responseBody),
};

// Future separate folders and files for scale
const Catalog = {
    list: (params: URLSearchParams) => requests.get("products", params),
    details: (id: number) => requests.get(`products/${id}`),
    fetchFilters: () => requests.get("products/filters"),
};

const Basket = {
    get: () => requests.get("basket"),
    addItem: (productId: number, quantity = 1) =>
        requests.post(`basket?productId=${productId}&quantity=${quantity}`, {}),
    removeItem: (productId: number, quantity = 1) =>
        requests.delete(`basket?productId=${productId}&quantity=${quantity}`),
};

const TestErrors = {
    get400Error: () => requests.get("buggy/bad-request"),
    get401Error: () => requests.get("buggy/unauthorized"),
    get404Error: () => requests.get("buggy/not-found"),
    get500Error: () => requests.get("buggy/server-error"),
    getValidationError: () => requests.get("buggy/validation-error"),
};

const Account = {
    login: (values: any) => requests.post("account/login", values),
    register: (values: any) => requests.post("account/register", values),
    currentUser: () => requests.get("account/currentUser"),
    fetchAddress: () => requests.get("account/savedAddress"),
};

const Orders = {
    list: () => requests.get("orders"),
    fetchOrder: (id: number) => requests.get(`orders/${id}`),
    create: (values: any) => requests.post("orders", values),
};

const Payment = {
    createPaymentIntent: () => requests.post("payments", {}),
};

const apiService = {
    Catalog,
    TestErrors,
    Basket,
    Account,
    Orders,
    Payment,
};

export default apiService;
